:root {
  --color-primary: #27343d;
  --color-primary-2: #fff;
  --color-primary-3: #27343d2d;
  --color-primary-4: #27343d8f;
  --color-primary-5: #e2e5e4;
  --color-dark: #0f0f0f;
  --color-dark-2: #0f0f0f7a;
  --color-gray-dark: #535c62;
  --color-gray: #c7c7c7;
  --color-gray-2: #d4d4d4;
  --color-gray-3: #bababa;
  --shadow-dark: 0 2rem 6rem #0000004d;
  --shadow-light: 0 2rem 5rem #0000000f;
  --line: 1px solid var(--color-grey-light-2);
  --radius: 33px;
  --radius-2: 10px;
  --shadow-black-1: #00000007;
  --shadow-black-2: #00000021;
  --color-dark-shadow: #0f0f0ff6;
  --shadow-white: #f5f5f515;
  --color-primary-shadow: #27343d2d;
  --color-primary-shadow-2: #27343d05;
}

@keyframes FadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes FadeIn2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: .9;
  }
}

@keyframes zoomOut {
  0% {
    transform: translate(-50%, -50%)scale(1.9);
  }

  100% {
    transform: translate(-50%, -50%)scale(1.4);
  }
}

@keyframes zoomIn {
  0% {
    transform: scale(.8);
  }

  100% {
    transform: scale(1.89);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

@media only screen and (width <= 80.5em) {
  html {
    font-size: 56.25%;
  }
}

@media only screen and (width <= 56.25em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (width >= 80.5em) {
  html {
    font-size: 50%;
  }
}

@media only screen and (width >= 114em) {
  html {
