  }
}

body {
  box-sizing: border-box;
  background-color: #fff;
  margin: 0 12.5rem;
  overflow-x: hidden;
}

@media only screen and (width <= 80.5em) {
  body {
    margin: 0;
  }
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

::selection {
  background-color: var(--color-primary);
  color: var(--color-gray);
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--color-gray);
}

::-webkit-scrollbar-thumb {
  background: var(--color-primary);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--color-primary-3);
}

.shadow {
  opacity: 0;
  background: linear-gradient(#0003, #0000);
  width: 100%;
  height: 100%;
  animation: 1.2s ease-in-out forwards FadeIn;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.shadow:after {
  content: "";
  background: linear-gradient(to top, #00000080, #0000);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.shadow:before {
  content: "";
  background: linear-gradient(to right, var(--color-primary), #0000);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

@media only screen and (width <= 47.75em) {
  .shadow {
    display: none;
  }
}

.spinner-container {
  z-index: 999;
  opacity: 1;
  visibility: visible;
  background-color: #e4f1fb;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  transition: opacity .5s, visibility .4s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.spinner-container.hidden {
  opacity: 0;
  visibility: hidden;
}

.spinner-video {
  object-fit: cover;
  width: 12.5rem;
  position: relative;
}

.spinner-video:before {
  content: "";
  background-color: red;
  width: 100%;
  height: 100%;
}

#loading-bar-container {
  background-color: #fff;
  width: 10%;
  height: 3px;
  position: absolute;
  top: 55%;
}

#loading-bar {
  background-color: var(--color-primary);
  border-radius: 50px;
  width: 0;
  height: 100%;
  transition: width .1s, background-color .1s;
}

