.container {
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  gap: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 10.1rem 6.1rem;
  display: grid;
}

.box1 {
  background-color: #f80e0f80;
  grid-area: 1 / 1 / 4 / 3;
}

.box2 {
  background-color: #9a997a80;
  grid-area: 1 / 3 / 2 / 5;
}

.box3 {
  background-color: #81cff380;
  grid-area: 2 / 3 / 3 / 4;
}

.box4 {
  background-color: #99c73380;
  grid-area: 2 / 4 / 3 / 5;
}

.box1, .box2, .box3, .box4 {
  color: #fff;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  display: flex;
}

@media (width <= 768px) {
  .container {
    grid-template-rows: repeat(5, 1fr);
    grid-template-columns: 1fr;
    grid-template-areas: "box1"
                         "box2"
                         "box3"
                         "box4"
                         "box5";
  }
}

