
.header_contianer {
  height: fit-content;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start; /* Align content to the right */
  overflow: hidden;
  z-index: 1;
  height: 100vh;
  max-width: 100%;
  margin-top: 0rem;
  z-index: 1;
  @include respond(tab-land) {
    align-items: center;
    justify-content: center; /* Align content to the right */
    margin: auto;
  }
  
}


.header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  align-content: flex-start;
  height: 100vh;
  max-width: 60rem;
  @include respond(tab-land) {
    align-items: center;
    justify-content: center; /* Align content to the right */
    align-content: center;
    max-width: 87.5%;
  }
  @include respond(phone) {
    position: relative;
    background-color: transparent;
    margin-top: 4%;
    justify-content: space-between;
    padding: 9rem 0rem 3.2rem 2.8rem;
    max-width: 89.5%;
    border-radius: var(--radius);
    height: 75vh;
    align-self: center;
    opacity: 0;
    animation: FadeIn 1.1s ease-in-out 0s forwards; /* Animation name, duration, easing, delay */

  }

  &__spans {
    margin-bottom: 0.5rem;
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    

    @include respond(tab-land) {
      margin-right: auto;
      padding-bottom: 1.2rem;
    }
    &--Solan {
      border-radius: 7px;
      padding: 0.6rem 1.1rem;
      color: var(--color-primary-2);
      text-transform: uppercase;
      align-self: center;
      font-weight: 400;
      letter-spacing: 1.5px;
      background-color: rgba(117, 117, 117, 0.945);
      margin-right: 1.2rem;
      @include respond(tab-land) {
        background-color: var(--color-gray);
        color: var(--color-primary);
      }
      @include respond(phone) {
        padding: 0.4rem 1.1rem;
        font-size: 1.2rem;
        background-color: #535C62;
        color: var(--color-primary-2);

      }
    }
    &--Est {
      color: var(--color-gray);
      align-self: center;

      @include respond(tab-land) {
        color: var(--color-gray);
        font-size: 1.3rem;
        font-weight: 700;
      }
      @include respond(phone) {
        font-size: 1.5rem;
      }
    }
  }
  &__title {
    font-size: 6.4rem;
    font-weight: 700;
    letter-spacing: 1.7px;
    line-height: 1.2;
    color: var(--color-primary-2);
    padding-right:  2.8rem ;
    margin: 2rem 0 ;
    @include respond(mid-desktop) {
      font-size: 6rem;
    }
    @include respond(tab-land) {
      margin-right: auto;
    }
    @include respond(phone) {
      margin-top: -0.5rem;
      font-size: 3.8rem;
      margin-bottom: 0rem ;
    }
    span {
      font-weight: 300 !important;
    }
  }
  &__paragraph {
    font-size: 2rem;
    color: var(--color-gray);
    margin: 1.8rem 0 6rem;
    padding-right:  2.8rem ;
    font-weight: 400;
    width: 100%;
    @include respond(tab-land) {
      margin: 2.5rem auto 6rem 0;
      width: 80%;
      color: var(--color-gray-2);
    }
    @include respond(phone) {
      font-size: 1.7rem;
      line-height: 2;
      font-weight: 300;
      margin-top: 1.8rem;
      width: 95%;
    }
  }
  &__buttons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include respond(tab-land) {
      justify-content: space-around;
      margin: 0 auto 0 0;
    }

    &__text {
      display: flex;

      align-items: center;
      align-content: center;
      margin-left: 4rem;
      align-self: center;
      position: relative;
      @include respond(tab-land) {
        margin-left: 3rem;
      }

      &_underline {
        font-size: 1.8rem;

        font-weight: 300;

        margin-right: 1rem;
        transition: margin-right 0.6s ease-out;
        @include respond(phone) {
          font-weight: 600;
          font-size: 1rem;
        }
      }

      &_arrow {
        font-size: 2.1rem;
        position: relative;
        bottom: 3px;
      }

      &::after {
        content: "";
        position: absolute;
        width: 100%;
        transform: scaleX(1);
        height: 1px;
        bottom: 6px;

        left: 0;
        background-color: rgb(255, 255, 255);
        transform-origin: bottom right;
        transition: transform 0.3s ease-out;
      }
      &:hover::after {
        transform-origin: bottom left;
      }
      &:hover &_underline {
        margin-right: 2rem;
      }
    }
  }
}
.header__title,
.header__paragraph,
.header__buttons,
.header__spans {
  opacity: 0;
}
#target {
  opacity: 0;
}

.mobileBtnArrrow {
  display: none;
}
