.bg-video--desktop {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  z-index: -1;

  &__content_desk {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%) scale(1.9);
    transform-origin: 80% 10%;
    animation: zoomOut 7s ease-in-out 2s forwards;
    @include respond(phone) {
      object-position: 70% 10%; 
    }
  }
}

.bg-video--mobile {
  position: absolute;
  top: 0;
  left: 0;
  background-color: var(--color-primary-2);
  z-index: 1;
  width: 100%;
  height: 100%;
  z-index: -1;
  overflow: hidden;
  border-radius: var(--radius);
  
