@keyframes FadeIn {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes FadeIn2 {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.9;
  }
}
@keyframes zoomOut {
  0% {
    transform: translate(-50%, -50%) scale(1.9); /* Adjust the scale as needed */
  }
  100% {
    transform: translate(-50%, -50%) scale(1.4);
  }
}
@keyframes zoomIn {
  0% {
    transform:scale(0.8); /* Adjust the scale as needed */
  }
  100% {
    transform: scale(1.89);
  }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
